import "./card.css";
import PropTypes from "react";

const Card = ({ lang, img, fcolor, scolor }) => {
  console.log(PropTypes);

  return (
    <div className="card" style={{ background: scolor, color: fcolor }}>
      <img src={img} alt={lang} />
      <h3>{lang}</h3>
    </div>
  );
};

Card.propTypes = {
  lang: PropTypes.string,
};

export default Card;
